.inventoryHeadDiv{
    display: flex;
    justify-content: space-around;
    align-items: baseline;
    margin-bottom: 30px;
}
.inventoryHeadDiv h2{
    font-size: 32px;
}
.inventoryButton{
    margin: 0;
    padding: 7px 20px;
    font-size: 14px;
    font-weight: 500;
    color: #f9f9f9;
    background-color: #080708;
    border-radius: 5px;
}
.inventoryButton:hover{
    background-color: #072AC8;
}
.inventory-table{
    width: 80%;
    margin: 0 auto;
    border: 1px solid #080708;
}

.inventory-table th{
    font-size: 16px;
    padding: 15px;
    border: 1px solid #080708;
}


.inventory-table td{
    font-size: 16px;
    border: 1px solid #080708;
    text-align: center;
    padding: 10px;

}
.deleteIcon{
    font-size: 22px;
    color: rgb(119, 8, 8);
}